.login {
  width: 100%;
  height: 100%;
  position: relative;
}
.login-con {
  position: absolute;
  right: 50%;
  top: 50%;
  width: 300px;
}
.login-con-header {
  font-size: 16px;
  font-weight: 300;
  text-align: center;
  padding: 30px 0;
}
.login-con .form-con {
  padding: 10px 0 0;
}
.login-con .login-tip {
  font-size: 10px;
  text-align: center;
  color: #c3c3c3;
}

